.header {
  background-color: var(--softRed);
  padding: 0.9375rem 1.5625rem;
  width: 95%;
  margin: 0.9375rem auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 1.25rem;
}
@media (min-width: 90em) {
  .header {
    width: 50%;
  }
}
.header__title {
  line-height: 1.6;
  color: var(--veryPaleOrange);
}
.header__title p {
  font-weight: 400;
  font-size: 1.25rem;
}
.header__title h1 {
  font-weight: 700;
}
.header__image {
  max-width: 100%;
  height: auto;
}

.main {
  width: 95%;
  margin: 0 auto;
  padding: 0.625rem;
  background-color: var(--veryPaleOrange);
  border-radius: 0.625rem;
}
@media (min-width: 90em) {
  .main {
    width: 50%;
  }
}
.main__spending {
  padding: 0.625rem;
  margin-bottom: 1.25rem;
  text-align: start;
}
.main__graph {
  display: flex;
  flex-direction: column;
  padding: 0.3125rem;
}
.main__graph--elements {
  width: 100%;
  height: 200px;
  position: relative;
}
.main__graph--elements .money {
  display: none;
  position: absolute;
  text-align: center;
  width: 50px;
  padding: 0.4375rem;
  margin-bottom: 0.625rem;
  background-color: var(--darkBrown);
  color: var(--veryPaleOrange);
  border-radius: 0.3125rem;
  font-size: 0.75rem;
  font-weight: 700;
  animation-name: hover;
  animation-duration: 0.3s;
}
@keyframes hover {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.2;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.main__graph--elements .money__mon {
  left: -2px;
  top: 65px;
}
.main__graph--elements .money__tue {
  left: 45px;
  top: 25px;
}
.main__graph--elements .money__wed {
  left: 91px;
  top: -35px;
}
@media (min-width: 90em) {
  .main__graph--elements .money__wed {
    left: 92px;
  }
}
.main__graph--elements .money__thu {
  left: 140px;
  top: 35px;
}
.main__graph--elements .money__fri {
  left: 185px;
  top: 55px;
}
@media (min-width: 90em) {
  .main__graph--elements .money__fri {
    left: 187px;
  }
}
.main__graph--elements .money__sat {
  left: 233px;
  top: 5px;
}
.main__graph--elements .money__sun {
  left: 279px;
  top: 45px;
}
@media (min-width: 90em) {
  .main__graph--elements .money__sun {
    left: 281px;
  }
}
.main__graph--elements .amounts {
  display: inline-block;
  width: 10%;
  padding: 0.625rem;
  background-color: var(--softRed);
  margin: 0 0.30625rem;
  border-radius: 2px;
}
@media (min-width: 90em) {
  .main__graph--elements .amounts {
    width: 11%;
    margin: 0 0.20625rem;
  }
}
.main__graph--elements .amounts:hover {
  opacity: 0.6;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.main__graph--elements .active {
  background-color: var(--cyan);
  transition: all 0.1s ease-in;
}
.main__graph--days {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  font-weight: 400;
  font-size: 1.125rem;
  color: var(--mediumBrown);
  margin-top: -1.25rem;
}
.main__graph--days .mon {
  position: absolute;
  left: 1%;
}
.main__graph--days .tue {
  position: absolute;
  left: 17%;
}
.main__graph--days .wed {
  position: absolute;
  left: 30%;
}
.main__graph--days .thu {
  position: absolute;
  left: 46%;
}
.main__graph--days .fri {
  position: absolute;
  left: 62%;
}
.main__graph--days .sat {
  position: absolute;
  left: 75%;
}
.main__graph--days .sun {
  position: absolute;
  left: 89%;
}
.main__total {
  border-top: 0.1875rem solid var(--cream);
  padding: 0.625rem;
  margin-top: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main__total--thisMonth {
  line-height: 1.6;
}
.main__total--thisMonth span {
  color: var(--mediumBrown);
  font-weight: 400;
}
.main__total--thisMonth h3 {
  color: var(--darkBrown);
  font-weight: 700;
  font-size: 1.875rem;
}
.main__total--difrence {
  padding-top: 1.5625rem;
}
.main__total--difrence h4 {
  text-align: end;
  color: var(--darkBrown);
  font-weight: 700;
}
.main__total--difrence p {
  color: var(--mediumBrown);
  font-weight: 400;
}

:root {
  --font-DM-sans: "DM Sans", sans-serif;
  /* Font size: 18px */
}

:root {
  /* Primary Colors: */
  --softRed: hsl(10, 79%, 65%);
  --cyan: hsl(186, 34%, 60%);
  /* Neutral Colors: */
  --darkBrown: hsl(25, 47%, 15%);
  --mediumBrown: hsl(28, 10%, 53%);
  --cream: hsl(27, 66%, 92%);
  --veryPaleOrange: hsl(33, 100%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-DM-sans);
  background-color: var(--cream);
  font-size: 18px;
}
@media (min-width: 90em) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50%;
    margin: 50px auto;
  }
}